import getTagsFromRemote from '@birdviewdev/git-tag/getTagsFromRemote';
import { mergeConfig } from '@birdviewdev/merge-config';

const AUTH_TOKEN = process.env.SENTRY_AUTH_TOKEN;
const RELEASE = process.env.NEXT_PUBLIC_APP_REPO_TAG || process.env.REACT_APP_APP_REPO_TAG;
const ENVIRONMENT = process.env.NEXT_PUBLIC_DEPLOY_ENV ||
    process.env.REACT_APP_DEPLOY_ENV ||
    'local';
const IS_BUILD = process.env.NODE_ENV === 'production';
console.log('IS_BUILD::', IS_BUILD);
/**
 * authToken: SENTRY_AUTH_TOKEN
 *
 * release: NEXT_PUBLIC_APP_REPO_TAG || REACT_APP_APP_REPO_TAG
 *
 * environment: NEXT_PUBLIC_DEPLOY_ENV || REACT_APP_DEPLOY_ENV (default: local)
 *
 * enabled: environment production, development, stage    true
 */
const withDefaultWebpackConfig = async (params = {}) => {
    const enabled = ['production', 'development', 'stage'].includes(ENVIRONMENT) &&
        process.env.NODE_ENV;
    const edges = await getNodeEdges(RELEASE);
    const currEdgeRef = edges.find((el) => el.node.name === RELEASE)?.node.target.oid ?? 'HEAD';
    const prevEdgeRef = next(edges, (el) => el.node.name === RELEASE)?.node.target.oid ?? 'HEAD^';
    const enableDefaultConfiguration = {
        silent: true,
        dryRun: !IS_BUILD,
        authToken: AUTH_TOKEN,
        ignore: ['node_modules'],
        stripPrefix: ['webpack://_N_E/'],
        include: [
            {
                paths: ['build'],
            },
            {
                paths: ['./.next/server'],
                urlPrefix: '~/_next/server',
            },
            {
                paths: ['./.next/static'],
                urlPrefix: '~/_next/static',
            },
        ],
        ...(RELEASE
            ? {
                // Invalid release version. Slashes and certain whitespace characters are not permitted.
                release: RELEASE.replace(/\//g, '-'),
                setCommits: {
                    auto: false,
                    repo: 'birdviewdev/fe-monorepo',
                    commit: currEdgeRef,
                    previousCommit: prevEdgeRef,
                    ignoreMissing: true,
                },
            }
            : {
                setCommits: {
                    auto: true,
                    ignoreMissing: true,
                },
            }),
    };
    const inactiveConfiguration = {
        silent: true,
    };
    return mergeConfig(enabled ? enableDefaultConfiguration : inactiveConfiguration, params);
};
const getNodeEdges = async (tagname) => {
    if (!tagname)
        return [];
    try {
        const tags = await getTagsFromRemote({
            filterBy: {
                tagname: tagname.substring(0, tagname.lastIndexOf('/')),
            },
            orderBy: {
                field: 'TAG_COMMIT_DATE',
                direction: 'DESC',
            },
            pagination: {
                pageSize: {
                    // MAXIMUM 100
                    first: 100,
                },
            },
        });
        return tags;
    }
    catch (e) {
        console.error(e);
        return [];
    }
};
const next = (arr, callback) => {
    if (!arr.some(callback)) {
        return undefined;
    }
    return arr.slice(arr.findIndex(callback) + 1).shift();
};

export { withDefaultWebpackConfig as default };
