import { init as initApm } from '@elastic/apm-rum';
import getConfig from 'next/config';

const { publicRuntimeConfig } = getConfig();

export { apm as APM } from '@elastic/apm-rum';

export const initializeAPM = (): void => {
  const isClientSide = typeof window !== 'undefined';
  const hasElasticAPMConfig = [
    publicRuntimeConfig.ELASTIC_APM_SERVICE_NAME,
    publicRuntimeConfig.ELASTIC_APM_ENVIRONMENT,
    publicRuntimeConfig.ELASTIC_APM_SERVER_URL,
  ].some((value) => typeof value !== 'undefined');

  if (!isClientSide || !hasElasticAPMConfig) {
    return;
  }

  initApm({
    serviceName: publicRuntimeConfig.ELASTIC_APM_SERVICE_NAME,
    environment: publicRuntimeConfig.ELASTIC_APM_ENVIRONMENT,
    serverUrl: publicRuntimeConfig.ELASTIC_APM_SERVER_URL,
    centralConfig: true,
  });
};
