import { mergeConfig } from '@birdviewdev/merge-config';

// This file configures the initialization of Sentry on the browser.
const SENTRY_DSN = process.env.NEXT_PUBLIC_SENTRY_DSN || process.env.REACT_APP_SENTRY_DSN;
const ENVIRONMENT = process.env.NEXT_PUBLIC_DEPLOY_ENV ||
    process.env.REACT_APP_DEPLOY_ENV ||
    'local';
const RELEASE = process.env.NEXT_PUBLIC_APP_REPO_TAG || process.env.REACT_APP_APP_REPO_TAG;
/**
 * dsn: NEXT_PUBLIC_SENTRY_DSN || REACT_APP_SENTRY_DSN
 *
 * environment: NEXT_PUBLIC_DEPLOY_ENV || REACT_APP_DEPLOY_ENV (default: local)
 *
 * enabled: environment production, development, stage    true
 */
const withDefaultInitConfig = (params) => {
    const defaultConfiguration = {
        enabled: ['production', 'development', 'stage'].includes(ENVIRONMENT),
        environment: ENVIRONMENT,
        replaysSessionSampleRate: 0,
        replaysOnErrorSampleRate: 0.25,
        ...(RELEASE ? { release: RELEASE.replaceAll('/', '-') } : {}),
        dsn: SENTRY_DSN,
    };
    return mergeConfig(defaultConfiguration, params);
};

export { withDefaultInitConfig as default };
